import React from 'react';
import './App.css';

class Square extends React.Component {
  render() {
    return (
      <button
        className={"square " + this.props.className}
        onClick={() => this.props.onClick()}
      >
        {this.props.value}
      </button>
    );
  }
}

class Board extends React.Component {
  renderSquare(i) {
    let className = this.props.lines.includes(i) ? 'square-win' : ''
    if (this.props.squares[i]) {
      className += this.props.squares[i] === 'X' ? ' square-x' : ' square-o'
    }
    console.log(this.props.squares)
    return (
      <Square
        className={className}
        value={this.props.squares[i]}
        onClick={() => this.props.onClick(i)}
      />
    );
  }

  render() {
    return (
      <div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
      </div>
    );
  }
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      history: [
        {
          squares: Array(9).fill(null)
        }
      ],
      stepNumber: 0,
      xIsNext: true,
      isStart: false,
      player: null,
      operatorClick: false
    };
  }


  handleClick = (i) => {
    const history = this.state.history.slice(0, this.state.stepNumber + 1);
    const current = history[history.length - 1];
    const squares = current.squares.slice();
    if (calculateWinner(squares) || squares[i]) {
      return;
    }
    squares[i] = this.state.xIsNext ? "X" : "O";
    this.setState({
      history: history.concat([
        {
          squares: squares
        }
      ]),
      stepNumber: history.length,
      xIsNext: !this.state.xIsNext
    });
  }

  jumpTo = (step) => {
    this.setState({
      stepNumber: step,
      xIsNext: this.state.operatorClick ? (step % 2) === 0 : (step % 2) !== 0
    });
  }

  startGame = () => {
    this.state.player && this.setState({
      isStart: true
    })
  }

  setPlayer = (evt) => {
    let player = evt.target.value
    this.setState({
      player: player,
      xIsNext: player === 'X',
      operatorClick: player === 'X'
    })
  }

  resetGame = () => {
    this.setState({
      history: [
        {
          squares: Array(9).fill(null)
        }
      ],
      stepNumber: 0,
      xIsNext: this.state.player === 'X'
    })
  }

  reselectPlayer = () => {
    this.setState({
      history: [
        {
          squares: Array(9).fill(null)
        }
      ],
      stepNumber: 0,
      xIsNext: true,
      isStart: false,
      player: null,
      operatorClick: false
    })
  }

  render() {
    const history = this.state.history;
    const current = history[this.state.stepNumber];
    const winner = calculateWinner(current.squares);

    const moves = history.map((step, move) => {
      const desc = move ?
        'Go to move #' + move :
        'Go to game start';
      return (
        <li key={move}>
          <button onClick={() => this.jumpTo(move)}>{desc}</button>
        </li>
      );
    });

    let status;
    if (winner) {
      status = "Winner: " + winner.playerWinner;
    } else {
      if (history.length > 9) status = "No winner: X and O tied ";
      else status = "Next player: " + this.state.player;
    }

    let renderAreaPlay = () => {
      return (
        <div className="game-play">
          <div className="game-board">
            <Board
              lines={winner ? winner.lines : []}
              squares={current.squares}
              onClick={i => this.handleClick(i)}
            />
            <div className="game-about">
              <div className="item item-yellow">Player X</div>
              <div className="item item-green">Player O</div>
              <div className="item item-red">Player Winner</div>
            </div>
          </div>
          <div className="game-info">
            <div>{status}</div>
            <ol>{moves}</ol>
          </div>
          <div className="game-reset">
            <p>Reset history</p>
            <button onClick={this.resetGame}>Reset</button>
          </div>
          <div className="game-reselect">
            <p>Re-select Players</p>
            <button onClick={this.reselectPlayer}>Re-select Players</button>
          </div>
        </div>
      );
    }

    let renderSelectPlayer = () => {
      return (
        <div className="game-player">
          <p className="game-player--title">Choose player start</p>
          <ul className="list-player" onChange={this.setPlayer}>
            <li className="player-item">
              <input type="radio" id="playerX" name="player" value="X"/>
              <label htmlFor="playerX">X</label>
            </li>
            <li className="player-item">
              <input type="radio" id="playerO" name="player" value="O"/>
              <label htmlFor="playerO">O</label>
            </li>
          </ul>
          <button disabled={!this.state.player} onClick={this.startGame}>Start Game</button>
        </div>
      )
    }

    return (
      <div className="game">
        { this.state.isStart ? renderAreaPlay() : renderSelectPlayer() }
      </div>
    );
  }
}

function calculateWinner(squares) {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return {
        playerWinner: squares[a],
        lines: lines[i]
      };
    }
  }
  return null;
}

export default App;
